.app {
    margin: 20px;

    table {
        border-collapse: collapse;
        margin: 25px 0;
        font-size: 14px;
        min-width: 400px;
        overflow: hidden;
        background-color: #eee;
        box-shadow: 0 0 20px rgba($color: #000000, $alpha: 0.15);

        
        thead {
            tr {
                background-color: #008777;
                color: #fff;
                text-align: left;
                font-weight: bold;
            }
        }

        tbody {
            tr {
                border-bottom: 1px solid #ddd;
            }

            tr:nth-of-type(even) {
                background-color: #f3f3f3;
            }

            tr:last-of-type {
                border-bottom: 2px solid #008777;
            }

            .rpzContainer {
                display: flex;
                flex-direction: column;
            }

            .active {
                display: none;
            }
        }

        th, td {
            padding: 12px 15px; 
        }

        .bullet::before {
            content: '\25CF';
            padding: 0px 6px;
            font-size: 1px;
            font-weight: bold;
            display: inline-block;
        }

        .empty::before {
            content: '\2800';
            padding: 0px 5px;
            font-size: 1px;
            font-weight: bold;
            display: inline-block;
        }

        .arrowRight::before {
            content: '\25B6';
            padding: 0px 5px;
            font-size: 11px;
            font-weight: bold;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
            transition: all 0.15s;
        }

        .arrowDown::before {
            transform: rotate(90deg);
          }
        
        
    }
}
